#titlesList {
    list-style-type: none;
    padding: 0 .8em;
    position: relative;
    display: grid;
    grid-template-rows: auto 1fr;
    overflow-x:hidden;
    overflow-y: scroll;
    height: 80vh !important;
}

#titlesList>li {
    color: #bf272f;
    width: 100%;
    height: max-content;
    padding: .3em;
    display: block;
    cursor: pointer;
    box-sizing: border-box;
    border-top: .1em solid gray;
    border-bottom: .1em solid gray;

    transition: .1s ease-in-out;
}

#titlesList>li:hover {
    transform: scale(1.05) translate(1em);
}

#titlesList>li.active {
    transform: scale(0.98);
}